import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { filterByType, getAllPokemon, getTypes, orderPokemon, filterByCreate } from '../../Redux/Actions/Actions.jsx';
import Card from '../Card/Card';
import './Home.css';

function Home() {

  const dispatch = useDispatch()

  const namePokemons = useSelector((state) => state.pokemons)
  const typesPokemons = useSelector((state) => state.types)


  const [, setOrder] = useState("")
  useEffect(() => {
    dispatch(getAllPokemon());
    dispatch(getTypes())
  }, [dispatch]);

  const handleOrderByName = (e) => {
    e.preventDefault();
    dispatch(orderPokemon(e.target.value))
    setOrder(`Order By: ${e.target.value}`)
  }

  const handleFilterByType = (e) => {
    e.preventDefault();
    dispatch(filterByType(e.target.value))
  }

  const handleFilterByCreation = (e) => {
    e.preventDefault();
    dispatch(filterByCreate(e.target.value))
  }

  return (
    <div className='Container-div'>
      <div>
        <label>Order By Name</label>
        <select onChange={(e) => { handleOrderByName(e) }}>
          <option value='ID'>ID</option>
          <option value='ABC'>A-Z</option>
          <option value='ZYX'>Z-A</option>
          <option value='ASC'>+ Attack</option>
          <option value='DESC'>- Attack</option>
        </select>
      </div>
      <div>
        <label>Filter By Type:</label>
        <select onChange={handleFilterByType}>
          <option value='ALL' >All</option>
          {typesPokemons.map((type) => (
            <option key={type.id} value={type}  >
              {type}
            </option>
          ))}
        </select>
      </div>
      <div>
        <label>Filter By Creation:</label>
        <select onChange={handleFilterByCreation}>
          <option value='JOIN' >All</option>
          <option value='API'>PokeApi</option>
          <option value='DB'>Created DB</option>

        </select>
      </div>
      <h1>Pokemons</h1>
      <div className='Container-card'>
        {namePokemons.length > 0 ? namePokemons?.map(({ name, id, types, image }) =>
          <Link to={`/pokemons/${id}`} key={id}  >
            <Card name={name} types={types} image={image} />
          </Link>
        ) : "Loading..."
        }
      </div>
    </div>
  )
}

export default Home