import './NavBar.css'
import { NavLink } from 'react-router-dom'
import Search from '../Search/Search'



function Navbar() {

  return (
    <div>
      <nav className='nav-Class'>
        <div>
          <Search/>
          <ul className='navList'>
            <NavLink to='/home'>
              <li>Home</li>
            </NavLink>
            <NavLink to='/create'>
            <li>Create Pokemon</li>
            </NavLink>
          </ul>
        </div>
      
      </nav>
    </div>
  )
}

export default Navbar