import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { getDetails, cleanDetails } from '../../Redux/Actions/Actions.jsx';
import { useEffect } from 'react';
import { useParams } from 'react-router';
import './PokemonDetails.css'


function PokemonDetails() {

    const { id } = useParams();

    const dispatch = useDispatch();

    const allDetails = useSelector((state) => state.pokemonDetail)

    useEffect(() => {
        dispatch(getDetails(id))
        return () => dispatch(cleanDetails())
    }, [dispatch, id])

    return (
        <div className='arre'>
            {allDetails.name ? (
                <div>
                    <h1>{id}</h1>
                    <h1>{allDetails.name}</h1>
                    <img src={allDetails.image} alt={allDetails.name} />
                    <h1>{allDetails.weight}</h1>
                    <h1>{allDetails.height}</h1>
                    <h1>{allDetails.types}</h1>
                    <h1>{allDetails.hp}</h1>
                    <h1>{allDetails.attack}</h1>
                    <h1>{allDetails.defense}</h1>
                    <h1>{allDetails.speed}</h1>
                </div>) : <h1>cargando</h1>}

        </div>
    )
}

export default PokemonDetails